import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink
} from "react-router-dom";
import { TransitionGroup, Transition } from "react-transition-group";
import { play, exit } from "./timelines";
import About from "./components/about";
import Projects from "./components/projects";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class App extends Component {
  render() {
    return (
      <Router>
        <nav className="navbar navbar-expand-lg navbar-light navbar-trans bg-transparent static-top mb-5">
          <div className="container">
            <Link to={"/"} className="navbar-brand">
              <span className="text-monospace">./Harsh Patel</span>
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarResponsive"
              aria-controls="navbarResponsive"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon" />
            </button>
            <div className="collapse navbar-collapse" id="navbarResponsive">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item">
                  <NavLink
                    exact={true}
                    to={"/"}
                    className="nav-link"
                    activeClassName="active"
                  >
                    About
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink to={"/projects"} className="nav-link">
                    Projects
                  </NavLink>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="mailto:pharsh58@gmail.com">
                    Contact{" "}
                    <FontAwesomeIcon icon={faExternalLinkAlt} fixedWidth />
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        <main className="container">
          <Route
            render={({ location }) => {
              const { pathname, key } = location;

              return (
                <TransitionGroup component={null}>
                  <Transition
                    key={key}
                    appear={true}
                    onEnter={(node, appears) => play(pathname, node, appears)}
                    onExit={(node, appears) => exit(node, appears)}
                    timeout={{ enter: 750, exit: 150 }}
                  >
                    <Switch location={location}>
                      <Route exact path="/" component={About} />
                      <Route path="/projects" component={Projects} />
                    </Switch>
                  </Transition>
                </TransitionGroup>
              );
            }}
          />
        </main>
        <footer className="footer">
          <div className="container">
            <span className="text-muted">Copyright &copy; 2019</span>
          </div>
        </footer>
      </Router>
    );
  }
}

export default App;
